<template>
  <div class="default-margin">
    <v-card class="default-padding">
      <v-btn @click.native="add">新增</v-btn>
    </v-card>
    <v-card class="default-padding" style="margin-top: 12px;">
      <v-data-table
        :headers="tableHeader"
        :items="list"
        :loading="loading"
        calculate-widths
        disable-pagination
        disable-filtering
        disable-sort
        hide-default-footer
      >
        <template v-slot:item.createDate="{ item }">
          {{ item.createDate | date }}
        </template>
        <template v-slot:item.operate="{ item }">
          <v-btn @click.native="remove(item.id)" color="error" small
          >删除</v-btn
          >
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
import { mapActions } from "vuex"
import { tableHeadersConstant } from "./script/constant"

export default {
  data() {
    return {
      loading: false,
      tableHeader: tableHeadersConstant(),
      list: []
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    ...mapActions("authorizeCode", ["ASave", "AList", "ARemove"]),
    async getList() {
      this.loading = true
      this.list = (await this.AList()).data
      this.loading = false
    },
    async remove(id) {
      let result = await this.ARemove({ id })
      if (result.status === 200) {
        this.$notify({ text: "成功", color: "success" })
        this.getList()
      } else {
        this.$notify({ text: result.message, color: "error" })
      }
    },
    async add() {
      try {
        let { input } = await this.$prompt({ placeholder: "请输入特殊操作码" })
        let result = await this.ASave({ code: input })
        if (result.status === 200) {
          this.$notify({ text: "成功", color: "success" })
          this.getList()
        } else {
          this.$notify({ text: result.message, color: "error" })
        }
        // eslint-disable-next-line no-empty
      } catch (e) {}
    }
  }
}
</script>

<style type="text/less" lang="less" scoped></style>
